body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Cantarell", "Droid Sans", "Helvetica Neue", sans-serif;
  color: white;
  background-color: #212121;
  /* background-color: #424242; */
}

main, nav {
  margin: 0 1rem;
}

nav {
  padding: 0.6rem 0;
}

.row {
  display: flex;
  width: 100%;
}

.row>div {
  flex-grow: 1;
  margin: 0;
  border: 1px dashed #888;
}

.control {
  border: none !important;
}

.control:nth-child(1) {
  width: 35%;
}

.control:nth-child(2) {
  text-align: center;
  width: 5%;
}

.control:nth-child(3) {
  width: 40%;
}

.control:nth-child(4) {
  width: 15%;
}

.idiom {
  padding: 1rem;
}

.idiom:nth-child(1) {
  width: 40%;
}

.idiom:nth-child(2) {
  width: 60%;
}
/* ========== COMMON ========== */

.tick text {
  fill: white;
}

.tick line {
  stroke: white;
}

/* ========== PLOT ========== */

.axis-x {
  fill: black;
  /* font-weight: bold; */
  text-anchor: end;
}

.axis-y {
  fill: black;
  /* font-weight: bold; */
  text-anchor: start;
}

.circles {
  fill: white;
  stroke: silver;
  stroke-width: 0.8;
  opacity: 0.7;
}

.text {
  fill: white;
  font-family: sans-serif;
  font-size: 10px;
}

/* ========== LINES ========== */

.line {
  fill: none;
  stroke-width: 2;
}

.dots {
  stroke-width: 2;
}

/* ========== MAP ========== */

.countries {
  fill: black;
}

.countries path {
  stroke: #ccf;
  opacity: 0.9;
  stroke-width: 0.4;
  stroke-opacity: 1;
}

.countries path:hover {
  stroke-width: 1.2;
  opacity: 1;
}

.names {
  fill: none;
  stroke: white;
  stroke-linejoin: round;
}

.names:hover {
  fill: black;
}

/* ========== CHORD ========== */
