/* ========== COMMON ========== */

.tick text {
  fill: white;
}

.tick line {
  stroke: white;
}

/* ========== PLOT ========== */

.axis-x {
  fill: black;
  /* font-weight: bold; */
  text-anchor: end;
}

.axis-y {
  fill: black;
  /* font-weight: bold; */
  text-anchor: start;
}

.circles {
  fill: white;
  stroke: silver;
  stroke-width: 0.8;
  opacity: 0.7;
}

.text {
  fill: white;
  font-family: sans-serif;
  font-size: 10px;
}

/* ========== LINES ========== */

.line {
  fill: none;
  stroke-width: 2;
}

.dots {
  stroke-width: 2;
}

/* ========== MAP ========== */

.countries {
  fill: black;
}

.countries path {
  stroke: #ccf;
  opacity: 0.9;
  stroke-width: 0.4;
  stroke-opacity: 1;
}

.countries path:hover {
  stroke-width: 1.2;
  opacity: 1;
}

.names {
  fill: none;
  stroke: white;
  stroke-linejoin: round;
}

.names:hover {
  fill: black;
}

/* ========== CHORD ========== */